/* cormorant-garamond-regular - cyrillic_latin */
@font-face {
  font-display: swap;
  font-family: 'Cormorant Mix';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/cormorant-garamond/cormorant-garamond-v16-cyrillic_latin-regular.eot');
  src: url('../fonts/cormorant-garamond/cormorant-garamond-v16-cyrillic_latin-regular.eot?#iefix') format('embedded-opentype'),
  url('../fonts/cormorant-garamond/cormorant-garamond-v16-cyrillic_latin-regular.woff2') format('woff2'),
  url('../fonts/cormorant-garamond/cormorant-garamond-v16-cyrillic_latin-regular.woff') format('woff'),
  url('../fonts/cormorant-garamond/cormorant-garamond-v16-cyrillic_latin-regular.ttf') format('truetype'),
  url('../fonts/cormorant-garamond/cormorant-garamond-v16-cyrillic_latin-regular.svg#CormorantGaramond') format('svg');
	unicode-range: U+0410-044F, U+002-002F, U+0040-007F;
}
